export const debounce = (func, wait = 20, immediate = true) => {
	//immediatetruedebouncewaitimmediatefunc
	//A.
    //B.immediatefuncwaitfuncwaitfunc

	let timeout;
	return function () {
		// reference the context and args for the setTimeout function
		let context = this, // this
			args = arguments; // array-like  fun

		//apply tothis

		//wait
		let later = function () {
			timeout = null; //waittimeoutnull ( timeoutID)
			if (!immediate) func.apply(context, args); //A.wait
		};

		// Should the function be called now?
		// If immediate is true + not already in a timeout --> then the answer is
		let callNow = immediate && !timeout;

		clearTimeout(timeout); //
		timeout = setTimeout(later, wait); //

		if (callNow) func.apply(context, args); //B.wait(20s)
	};
};

